(function ($, Drupal) {
  Drupal.behaviors.mainMenuBehaviors = {
    attach: function (context, settings) {
      "use strict";

      if (!$("html").hasClass("wb-disable")) {
        // Desktop
        if ($("#default-menu", context).is(":visible")) {
          var menubar = new Menubar(document.getElementById("default-menu"));
          menubar.init();
          // Make Hover Menu dismissable using Escape Key - WCAG Requirement.
          $(document).keyup(function (event) {
            if (event.key == "Escape") {
              // escape key maps to keycode `27`
              menubar.menubarItems.forEach(function (item) {
                if (item.popupMenu) {
                  item.popupMenu.close(true);
                }
              });
            }
          });
        }

        // // Mobile
        // if ($(".mobile-menu-wrapper", context).is(":visible")) {
        //   var mobilemenu = new DisclosureNav($("#mobile-menu")[0]);
        //   mobilemenu.init();
        //   mobilemenu.updateKeyControls(true);
        // }
      }
    },
  };
})(jQuery, Drupal);
